.chat{
    display:flex;
    justify-content: space-between;
    align-items :center;
    height:62px;
    border-top: 1px solid lightgray;
}

.chat > .input{
    flex: 1;
    display:flex;
 
}
.chat > .input > input{
    flex:1;
    border-radius: 30px;
    padding:10px;
    border:none;
}
.chat > .input > button{
    display:none;
}
.chat > .MuiSvgIcon-root{
    padding:10px;
    color:gray;
}